/* About.css */
.about-container {
    padding: 2rem;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}
.about-container h1 {
    color: #2c3e50;
    margin-bottom: 1rem;
}
.about-container p {
    color: #555;
    font-size: 1.1rem;
}
